<template>
    <section class="py-5" v-if="product">
        <div class="container">
            <div class="row">
                <div class="col-sm-12 col-lg-6">
                    <div class="w-100 bg-info bg-opacity-50" style="height: 600px">

                    </div>
                </div>
                <div class="col-sm-12 col-lg-6">
                    <div class="pl-3">
                        <h4 class="" v-text="product.name"></h4>
                        <div class="my-3">
                            <span class="text-black-50" v-text="product.category.name"></span>
                            <span class="text-black-50 fw-bold mx-2"> . </span>
                            <span class="text-black-50">
                                <i class="fa-solid fa-shopping-basket"></i>
                                154 orders
                            </span>
                            <span class="text-black-50 fw-bold mx-2"> . </span>
                            <span :class="product.inventory === 0 ? 'text-black-50 text-decoration-line-through' : 'text-success'" v-text="product.inventory === 0? 'Out of stock' : 'In stock'"></span>
                        </div>
                        <h3 class="mb-3" v-text="formatCurrency(product.price)"></h3>
                        <p class="text-black-50" v-text="product.description"></p>
                        <hr>
                        <div class="row mb-4">
                            <div class="col-md-4 col-6">
                                <label class="form-label">Size</label>
                                <select class="form-select">
                                    <option>Small</option>
                                    <option>Medium</option>
                                    <option>Large</option>
                                </select>
                            </div>
                            <div class="col-md-3 col-6">
                                <label class="form-label">Quantity</label>
                                <vue-number-input :min="1" v-model="quantity" :max="10" inline center controls></vue-number-input>
                            </div>
                        </div>
                        <a href="" class="btn btn-primary me-2">Buy Now</a>
                        <a href="" class="btn btn-secondary me-2">
                            <i class="fa-solid fa-shopping-basket"></i>
                            Add To Cart
                        </a>
                        <a href="" class="btn btn-light text-black-50">
                            <i class="fa fa-heart"></i>
                            Save
                        </a>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>
<script>
import VueNumberInput from "@chenfengyuan/vue-number-input";
export default {
    data(){
        return{
            quantity: 1,
        }
    },
    components: { VueNumberInput },
    computed:{
        products(){
            return this.$store.state.products;
        },
        product(){
            if (this.$route.params.slug){
                return this.$store.state.products.find(product => product.slug === this.$route.params.slug)
            }
        },
    },
    methods: {
        formatCurrency(price){
            price = (price/100);
            return price.toLocaleString("en-US",{style: "currency",currency: "USD"})
        },
    }
}
</script>
